import style from './Footer.module.css'
import styleContainer from "../common/container.module.css";
import {Button} from "../common/Button";
import {NameBlock} from "../common/NameBlock";

export const Footer = () => {
    return(
        <footer className={style.footer}>
            <div className={styleContainer.container}>
                <div className={style.footer__wrapper}>
                    <NameBlock name={'Anton Trishin'} style={style.footer__header}/>
                    <div className={style.social}>
                        <div className={style.social__icon}></div>
                        <div className={style.social__icon}></div>
                        <div className={style.social__icon}></div>
                        <div className={style.social__icon}></div>
                    </div>
                    <div className={style.footer__politic}><span>2019 Все права защищены</span></div>
                </div>
            </div>
        </footer>
    )
}



