import style from './Contacts.module.css'
import styleContainer from "../common/container.module.css";
import {Button} from "../common/Button";
import {NameBlock} from "../common/NameBlock";

export const Contacts = () => {
    return(
        <div className={style.contacts}>
            <div className={styleContainer.container}>
                <div className={style.contacts__wrapper}>
                    <NameBlock name={'Contacts'} style={style.contacts__header}/>
                    <form action="" className={style.contacts__form}>
                        <input type="text" name="name" required placeholder="Имя" className=""/>
                        <input type="tel" name="tel" required placeholder="Телефон" className="" />
                        <input type="text-area" name="area" required placeholder="Введите текст" className=""/>
                        <div className={style.contacts__button}>
                            <Button name={'Send'}/>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}



